import { BLOCKQUOTE_TOGGLE, BLOCKQUOTE_ENABLE, BLOCKQUOTE_DISABLE, BLOCKQUOTE_TRANSFORM } from '../actions';
import toggleBlockquote from '../commands/toggleQuote';
import enableBlockquote from '../commands/enableQuote';
import disableBlockquote from '../commands/disableQuote';
import transformBlockquote from '../commands/transform';

export default function onAction(action, editor, next) {
  const { type } = action;
  if (type === BLOCKQUOTE_TOGGLE) {
    return toggleBlockquote(editor);
  }
  if (type === BLOCKQUOTE_TRANSFORM) {
    return transformBlockquote(editor);
  }
  if (type === BLOCKQUOTE_ENABLE) {
    return enableBlockquote(editor);
  }
  if (type === BLOCKQUOTE_DISABLE) {
    return disableBlockquote(editor);
  }
  return next();
}
